'use client'

import * as React from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"

// Import JSON
import transactionsData from "@/data/transactions.json" assert { type: "json" }

interface Transaction {
  merchantName: string
  merchantAddress: string
  date: string
  time: string
  mid: string
  tid: string
  batchNo: string
  stan: string
  cardNumber: string
  cardType: string
  entryMode: string
  appCode: string
}

export default function Dashboard() {
  const [tid, setTid] = React.useState("")
  const [transactions, setTransactions] = React.useState<Transaction[]>(transactionsData)

  // Pagination
  const [currentPage, setCurrentPage] = React.useState(1)
  const itemsPerPage = 10 // show 10 items per page
  const totalPages = Math.ceil(transactions.length / itemsPerPage)

  const handleSearch = () => {
    if (!tid) return setTransactions(transactionsData)
    const filtered = transactionsData.filter((t) => t.tid.includes(tid))
    setTransactions(filtered)
    setCurrentPage(1) // reset to first page
  }

  // Paginated data
  const paginatedTransactions = transactions.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  )

  const handlePrev = () => {
    if (currentPage > 1) setCurrentPage(currentPage - 1)
  }

  const handleNext = () => {
    if (currentPage < totalPages) setCurrentPage(currentPage + 1)
  }

  return (
    <div className="space-y-6">
      {/* Search Card */}
      <Card className="p-4">
        <div className="flex flex-col md:flex-row gap-3 items-start md:items-center w-1/3">
          <Input
            placeholder="Enter TID..."
            value={tid}
            onChange={(e) => setTid(e.target.value)}
            className="flex-1"
          />
          <Button onClick={handleSearch}>Search</Button>
        </div>
      </Card>

      {/* Transaction Table */}
      <Card className="p-4 overflow-x-auto">
        <table className="w-full text-sm table-auto">
          <thead>
            <tr className="bg-muted">
              {[
                "Merchant Name",
                "Merchant Address",
                "Date",
                "Time",
                "MID",
                "TID",
                "Batch No",
                "STAN",
                "Card Number",
                "Card Type",
                "Entry Mode",
                "App Code",
              ].map((col) => (
                <th key={col} className="px-2 py-1 text-left">{col}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {paginatedTransactions.map((t, idx) => (
              <tr key={idx} className="hover:bg-muted/50 border-b transition-colors">
                <td className="px-2 py-1">{t.merchantName}</td>
                <td className="px-2 py-1">{t.merchantAddress}</td>
                <td className="px-2 py-1">{t.date}</td>
                <td className="px-2 py-1">{t.time}</td>
                <td className="px-2 py-1">{t.mid}</td>
                <td className="px-2 py-1">{t.tid}</td>
                <td className="px-2 py-1">{t.batchNo}</td>
                <td className="px-2 py-1">{t.stan}</td>
                <td className="px-2 py-1">{t.cardNumber}</td>
                <td className="px-2 py-1">{t.cardType}</td>
                <td className="px-2 py-1">{t.entryMode}</td>
                <td className="px-2 py-1">{t.appCode}</td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* Pagination Controls */}
        <div className="flex justify-end items-center mt-4 gap-4">
            <span>
            Page {currentPage} of {totalPages}
          </span>
          <Button onClick={handlePrev} disabled={currentPage === 1}>Previous</Button>
          
          <Button onClick={handleNext} disabled={currentPage === totalPages}>Next</Button>
        </div>
      </Card>
    </div>
  )
}
